'use strict';

module.exports = titleCase;
var stopwords = 'a an and at but by for in nor of on or so the to up yet';
var defaults = stopwords.split(' ');

function titleCase(str, options) {
  var opts = options || {};
  if (!str) return '';
  var stop = opts.stopwords || defaults;
  var keep = opts.keepSpaces;
  var splitter = /(\s+|[-‑–—])/;
  return str.split(splitter).map(function (word, index, all) {
    if (word.match(/\s+/)) return keep ? word : ' ';
    if (word.match(splitter)) return word;

    if (index !== 0 && index !== all.length - 1 && stop.includes(word.toLowerCase())) {
      return word.toLowerCase();
    }

    return capitalize(word);
  }).join('');
}

function capitalize(str) {
  return str.charAt(0).toUpperCase() + str.slice(1);
}